.btn {
  align-items: center;
  background-color: #4b4b4b;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  height: 50px;
  justify-content: center;
  position: fixed;
  right: 30px;
  top: 30px;
  transform: translateY(-30%);
  transition: .3s;
  width: 50px;
  z-index: 99;


  &:hover {
    background-color: #ffb400;
  }
}
